import { useState } from 'react';
import Table from 'react-bootstrap/Table';
import { useDispatch, useSelector } from 'react-redux';
import { addItem, addCart } from '../store'

function Cart() {

    let [cartgoods, addgoods] = useState([])
    let a = useSelector((state) => { return state.stock })
    let user = useSelector((state) => { return state.user })
    let dispath = useDispatch()

    return (
        <div>
            
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>상품명</th>
                        <th>수량</th>
                        <th>변경하기</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        a.map(function(stock, i){
                            return(
                                <tr>
                                <td>{a[i].id}</td>
                                <td>{a[i].name}</td>
                                <td>{a[i].count}</td>
                                <td>
                                    <button onClick={()=>{
                                       dispath(addItem(a[i].id))
                                    }}>+</button>
                                </td>
                                </tr>
                            )
                        })
                    }

                </tbody>
            </Table>
        </div>
    )
}

export default Cart;